const theme = {
  colors: {
    base: '#F3F3F3',
    primary: '#1B8CEB',
    success: '#72CF89'
  },
  textColors: {
    base: '#000000',
    primary: '#FFFFFF',
    success: '#FFFFFF'
  },
  borderColors: {
    base: '#CCCCCC',
    primary: '#106CB9',
    success: '#3BAB57'
  },
  shadow: '0px 0px 1px rgba(0, 0, 1, 0.12)',
  borderRadius: '5px'
}

export default theme



